import React from 'react';

const buildId = name => name.split(' ').join('-');

const Project = ({ name = 'Project name', children }) => (
  <li
    className="list-group-item"
    data-toggle="collapse"
    data-target={buildId(name)}
    aria-expanded="false"
    aria-controls={buildId(name)}
    role="button"
  >
    <h6>{name}</h6>
    <div className="collapse" id={buildId(name)}>
      {children}
    </div>
  </li>
);

export default Project;
